<template>
  <!-- screenDom画布的名称 -->
  <div class="canvas-container" ref="screenDom"></div>
</template>

<script setup>
// 导入函数
import * as THREE from "three";
import { onMounted, onUnmounted, ref } from "vue";
// 导入控制器（实现按鼠标旋转的）
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";

// 加载HDR贴图
// 导入加载器
import { RGBELoader } from 'three/examples/jsm/loaders/RGBELoader';

// 加载模型
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
// 模型解压
import { DRACOLoader } from "three/examples/jsm/loaders/DRACOLoader";

// 画布刚开始是空的，没有渲染
let screenDom = ref(null);

// 声明轨道控制器
let controls;


// 使用onMounted将渲染的内容添加到画布
onMounted(() => {
  //  创建场景
  let scene = new THREE.Scene();
  // // 2 创建透视相机
  let camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
  // // 摄影机的位置
  camera.position.set(0, 1.5, 6);
  // // 创建渲染器
  let renderer = new THREE.WebGLRenderer({
    // 抗锯齿
    antialias: true,
  });
  //设置渲染器的尺寸大小
  renderer.setSize(screenDom.value.clientWidth, screenDom.value.clientHeight);
  // 把渲染器插入到canvasDom,当画布加载完毕的时候，才可以渲染
  screenDom.value.appendChild(renderer.domElement);

  // 添加10X10的网格地面，等于号前面的是名称，后面的函数
  const gridHelper = new THREE.GridHelper(10, 10);
  // 修改网格的透明度
  gridHelper.material.opacity = 0.5;
  gridHelper.material.transparent = true;
  scene.add(gridHelper);

  //  创建辅助坐标轴
  const axesHelper = new THREE.AxesHelper(5);
  scene.add(axesHelper);

  // 创建轨道控制器
  controls = new OrbitControls(camera, renderer.domElement);
  controls.update();

  // 创建HDR
  const hdrLoader = new RGBELoader();
  // 加载HDR
  hdrLoader.loadAsync('./HDR/Bright_Michigan.hdr').then((teture) => {
    teture.mapping = THREE.EquirectangularReflectionMapping;
    // 场景显示HDRI贴图
    // scene.background = teture;
    // 场景上的物体受HDRI贴图的影响
    scene.environment = teture;
  });

 // 1.清除初始化渲染背景
 renderer.setClearColor("#000");
  // 设置渲染背景为灰色
  scene.background = new THREE.Color("#ccc");

  // 添加模型
  // 解压缩文件所在位置
  const dracLoader = new DRACOLoader();
  dracLoader.setDecoderPath("./draco/gltf/");
  // 解压缩后的文件类型
  dracLoader.setDecoderConfig({ type: "js" });
  // 解压后加载模型
  const gltfloader = new GLTFLoader();
  gltfloader.setDRACOLoader(dracLoader);
  // 加载模型地址
  gltfloader.load("./model/pingzi.glb", (gltf) => {
    scene.add(gltf.scene)
    gltf.scene.position.set(0, 0, 0);
  })


  // 把场景和相机都添加到渲染器
  function render() {
    renderer.render(scene, camera);
    // 多次执行渲染函数
    requestAnimationFrame(render);
  }
  render();

})


</script>

<style>
/* 删除画布上的边际 */
* {
  margin: 0;
  padding: 0;
}

.canvas-container {
  width: 100vw;
  height: 100vh;
}
</style>
